/*================ Typography ================*/
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	font-family: inherit;
	font-weight: 500;
	line-height: 1.1;
	color: inherit;
}

h1 small, h2 small, h3 small, h4 small, h5 small, h6 small, .h1 small, .h2 small, .h3 small, .h4 small, .h5 small, .h6 small, h1 .small, h2 .small, h3 .small, h4 .small, h5 .small, h6 .small, .h1 .small, .h2 .small, .h3 .small, .h4 .small, .h5 .small, .h6 .small {
	font-weight: 400;
	line-height: 1;
	color: #777;
}

h1, .h1, h2, .h2, h3, .h3 {
	margin-top: 14px;
	margin-bottom: 14px;
}

h1 small, .h1 small, h2 small, .h2 small, h3 small, .h3 small, h1 .small, .h1 .small, h2 .small, .h2 .small, h3 .small, .h3 .small {
	font-size: 65%;
}

h4, .h4, h5, .h5, h6, .h6 {
	margin-top: 14px;
	margin-bottom: 14px;
}

h4 small, .h4 small, h5 small, .h5 small, h6 small, .h6 small, h4 .small, .h4 .small, h5 .small, .h5 .small, h6 .small, .h6 .small {
	font-size: 75%;
}

p {
	margin: 0 0 14px;
}

.lead {
	margin-bottom: 28px;
	font-size: 23px;
	font-weight: 300;
	line-height: 1.4;
}

@media (min-width: 768px) {
	.lead {
		font-size: 30px;
	}
}
h1, .h1 {
	font-size: 36px/var(--default-font-size)0em;
}

h2, .h2 {
	font-size: 28px/var(--default-font-size)0em;
}

h3, .h3 {
	font-size: 22px/var(--default-font-size)0em;
}

h4, .h4 {
	font-size: 20px/var(--default-font-size)0em;
}

h5, .h5 {
	font-size: 16px/var(--default-font-size)0em;
}

h6, .h6 {
	font-size: 14px/var(--default-font-size)0em;
}

.section_title {
	margin-bottom: 23px;
}

.cole {
	padding-bottom: 35px;
}

.container-fluid {
	padding: 0 15px;
}

.main-content {
	margin-top: 160px;
}
.main-content.main-content--no-margin-desktop {
	margin-top: 0;
}
.main-content.main-content--no-margin {
	margin-top: 0;
}
@media (max-width: 991px) {
	.main-content {
		margin-top: 101px;
	}
	.main-content.main-content--no-margin-desktop {
		margin-top: 101px;
	}
	.main-content.main-content--no-margin {
		margin-top: 0;
	}
}
@media (max-width: 767px) {
	.main-content {
		margin-top: 142px;
	}
	.main-content.main-content--no-margin-desktop {
		margin-top: 142px;
	}
	.main-content.main-content--no-margin {
		margin-top: 0;
	}
}

.clearfix {
	*zoom: 1;
}
.clearfix:after {
	content: "";
	display: table;
	clear: both;
}

*, input, :before, :after {
	box-sizing: border-box;
}

html, body {
	padding: 0;
	margin: 0;
	-webkit-text-size-adjust: none;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
	display: block;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
	height: auto;
}

*, *:focus {
	outline: none;
}

html {
	background-color: var(--body-bg-color);
}

body {
	background-color: var(--body-bg-color);
}

.visually-hidden, .supports-fontface .icon-fallback-text .fallback-text {
	position: absolute;
	overflow: hidden;
	clip: rect(0 0 0 0);
	height: 1px;
	width: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
}

body,
input,
textarea,
button,
select {
	font-size: var(--default-font-size);
	font-family: var(--font-stack);
	color: var(--body-text-color);
	font-weight: 300;
	-webkit-font-smoothing: antialiased;
	-webkit-text-size-adjust: 100%;
}

p {
	margin: 0 0 15px 0;
}
p img {
	margin: 0;
}

em {
	font-style: italic;
}

b, strong {
	font-weight: bold;
}

/*============================================================================
  #Rich Text Editor
==============================================================================*/
.rte {
	margin-bottom: 15px;
}
.rte a {
	text-decoration: underline;
}
.rte h1, .rte .h1, .rte h2, .rte .h2, .rte h3, .rte .h3, .rte h4, .rte .h4, .rte h5, .rte .h5, .rte h6, .rte .h6 {
	margin-top: 2em;
}
.rte h1:first-child, .rte .h1:first-child, .rte h2:first-child, .rte .h2:first-child, .rte h3:first-child, .rte .h3:first-child, .rte h4:first-child, .rte .h4:first-child, .rte h5:first-child, .rte .h5:first-child, .rte h6:first-child, .rte .h6:first-child {
	margin-top: 0;
}
.rte h1 a, .rte .h1 a, .rte h2 a, .rte .h2 a, .rte h3 a, .rte .h3 a, .rte h4 a, .rte .h4 a, .rte h5 a, .rte .h5 a, .rte h6 a, .rte .h6 a {
	text-decoration: none;
}
.rte > div {
	margin-bottom: 15px;
}
.rte li {
	margin-bottom: 0.4em;
}

.rte--header {
	margin-bottom: 0;
}

/*============================================================================
  #Links and Buttons
==============================================================================*/
a,
.text-link {
	color: var(--color-primary);
	text-decoration: none;
	background: transparent;
}

button {
	overflow: visible;
}

.btn,
.rte .btn {
	display: inline-block;
	padding: 8px 10px;
	width: auto;
	margin: 0;
	line-height: 1.42;
	font-weight: bold;
	text-decoration: none;
	text-align: center;
	vertical-align: middle;
	white-space: nowrap;
	cursor: pointer;
	border: 1px solid transparent;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	border-radius: 0px;
	/*================ Set primary button colors - can override later ================*/
	background-color: var(--color-primary) !important;
	color: #fff !important;
}

/*============================================================================
  #Lists
==============================================================================*/
ul, ol {
	margin: 0 0 15px 20px;
	padding: 0;
}

ul ul, ul ol,
ol ol, ol ul {
	margin: 4px 0 5px 20px;
}

li {
	margin-bottom: 0.25em;
}

/*============================================================================
  #Tables
==============================================================================*/
table {
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
}

th, td {
	text-align: left;
	padding: 0;
	border: none;
}

/*============================================================================
  #Images and Iframes
==============================================================================*/
img {
	border: 0 none;
}

svg:not(:root) {
	overflow: hidden;
}

img,
iframe {
	max-width: 100%;
}

/*============================================================================
  #Forms
==============================================================================*/
form {
	margin-bottom: 0;
}

/*================ Prevent zoom on touch devices in active inputs ================*/
input,
textarea,
button,
select {
	padding: 0;
	margin: 0;
	-webkit-user-select: text;
	-moz-user-select: text;
	-ms-user-select: text;
	user-select: text;
}

button {
	background: none;
	border: none;
	cursor: pointer;
}

button,
input,
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
}

button {
	background: none;
	border: none;
	display: inline-block;
	cursor: pointer;
}

button,
input[type=submit] {
	cursor: pointer;
}

input,
textarea,
select {
	border: 1px solid var(--color-borders);
	max-width: 100%;
	padding: 8px 10px;
	border-radius: 0px;
}

/*================ Input element overrides ================*/
input[type=checkbox],
input[type=radio] {
	display: inline;
	margin: 0 8px 0 0;
	padding: 0;
	width: auto;
}

input[type=checkbox] {
	-webkit-appearance: checkbox;
	-moz-appearance: checkbox;
}

input[type=radio] {
	-webkit-appearance: radio;
	-moz-appearance: radio;
}

select {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background: -moz-linear-gradient(top, white 0%, rgba(255, 255, 255, 0) 100%);
	background: -webkit-linear-gradient(top, white 0%, rgba(255, 255, 255, 0) 100%);
	background: linear-gradient(to bottom, white 0%, rgba(255, 255, 255, 0) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff", endColorstr="#00ffffff", GradientType=0);
	background-position: right center;
	background-image: var(--down-arrow-png);
	background-repeat: no-repeat;
	background-position: right 10px center;
	background-color: #fafafa;
	padding-right: 25px;
	background-size: 10px;
	text-indent: 0.01px;
	text-overflow: "";
	cursor: pointer;
}

option {
	color: #000;
	background-color: #fff;
}

/*================ Form labels ================*/
label[for] {
	cursor: pointer;
}

/*============================================================================
  A generic way to visually hide content while
  remaining accessible to screen readers (h5bp.com)
==============================================================================*/
.icon:before {
	display: none;
}

/*================ Icon mapping ================*/
.icon-arrow-down:before {
	content: "";
}

.site-header__cart-toggle {
	display: inline-block;
}

