/*============================================================================
  #Ajax Cart Styles (conditionally loaded)
==============================================================================*/
.ajaxcart__inner {
	margin-bottom: 30px;
}

.ajaxcart__row > .grid {
	margin: 0 0 0 -15px;
}
.ajaxcart__row > .grid > .grid__item {
