/*================ Quantity Selectors ================*/
.js-qty__adjust, .ajaxcart__qty-adjust {
	cursor: pointer;
	position: absolute;
	display: block;
	top: 0;
	bottom: 0;
	border: 0 none;
	padding: 0 5px;
