// timber.scss.liquid START
// ----------------------
$viewportIncrement: 1px;
$small: 480px;
$medium: 767px;
$large: 768px;
$large_tab: 778px;
$large_laptop: 1170px;
$postSmall: $small + $viewportIncrement;
$preMedium: $medium - $viewportIncrement;
$preLarge: $large - $viewportIncrement;
$breakpoints: (
	'small' '(max-width: #{$small})',
	'medium' '(min-width: #{$postSmall}) and (max-width: #{$medium})',
	'medium-down' '(max-width: #{$medium})',
	'large' '(min-width: #{$large})'
);
$breakpoint-has-widths: ('small', 'medium', 'medium-down', 'large');
$breakpoint-has-push: ('medium', 'medium-down', 'large');
$breakpoint-has-pull: ('medium', 'medium-down', 'large');
// Colors
$colorPrimary: var(--color-primary);
$colorSecondary: var(--color-secondary);
// Button colors
$colorBtnPrimary: $colorPrimary;
$colorBtnPrimaryText: #fff;
$colorBtnSecondary: $colorSecondary;
$colorBtnSecondaryHover: darkenModified($colorSecondary, 0.1);
$colorBtnSecondaryActive: darkenModified($colorSecondary, 0.1);
$colorBtnSecondaryText: #fff;
// Text link colors
$colorLink: $colorPrimary;
// Text colors
$colorTextBody: var(--body-text-color);
// Backgrounds
$colorBody: var(--body-bg-color);
// Border colors
$colorBorder: var(--color-borders);
// Nav and dropdown link background
$colorNav: var(--header-menu-bg-color);
// Drawers
$drawerNavWidth: 300px;
$drawerCartWidth: 300px;
$colorDrawers: var(--header-menu-bg-color);
$colorDrawerBorder: darkenModified($colorDrawers, 0.05);
$colorDrawerText: var(--body-text-color);
$drawerTransition: all 0.4s cubic-bezier(0.46, 0.01, 0.32, 1);
// Sizing variables
$siteWidth: 1180px;
$gutter: 30px;
$gridGutter: 30px; // can be a %, but nested grids will have smaller margins because of it
$radius: 0px;
// Z-index
$zindexNavDropdowns: 5;
$zindexDrawer: 99999;
/*================ Typography ================*/
$headerFontWeight: 500;
$baseFontSize: var(--default-font-size);
$socialIconFontStack: 'icons';
$iconFontAwesome: 'FontAwesome';

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	font-family: inherit;
	font-weight: 500;
	line-height: 1.1;
	color: inherit
}

h1 small, h2 small, h3 small, h4 small, h5 small, h6 small, .h1 small, .h2 small, .h3 small, .h4 small, .h5 small, .h6 small, h1 .small, h2 .small, h3 .small, h4 .small, h5 .small, h6 .small, .h1 .small, .h2 .small, .h3 .small, .h4 .small, .h5 .small, .h6 .small {
	font-weight: 400;
	line-height: 1;
	color: #777
}

h1, .h1, h2, .h2, h3, .h3 {
	margin-top: 14px;
	margin-bottom: 14px
}

h1 small, .h1 small, h2 small, .h2 small, h3 small, .h3 small, h1 .small, .h1 .small, h2 .small, .h2 .small, h3 .small, .h3 .small {
	font-size: 65%
}

h4, .h4, h5, .h5, h6, .h6 {
	margin-top: 14px;
	margin-bottom: 14px
}

h4 small, .h4 small, h5 small, .h5 small, h6 small, .h6 small, h4 .small, .h4 .small, h5 .small, .h5 .small, h6 .small, .h6 .small {
	font-size: 75%
}

p {
	margin: 0 0 14px
}

.lead {
	margin-bottom: 28px;
	font-size: 23px;
	font-weight: 300;
	line-height: 1.4
}

@media (min-width: 768px) {
	.lead {
		font-size: 30px
	}
}

@function em($target, $context: $baseFontSize) {
	@if $target == 0 {
		@return 0;
	}
	@return $target / $context + 0em;
}

h1 {
	font-size: em(36px);
}

h2 {
	font-size: em(28px);
}

h3 {
	font-size: em(22px);
}

h4 {
	font-size: em(20px);
}

h5 {
	font-size: em(16px);
}

h6 {
	font-size: em(14px);
}

.h1 {
	@extend h1;
}

.h2 {
	@extend h2;
}

.h3 {
	@extend h3;
}

.h4 {
	@extend h4;
}

.h5 {
	@extend h5;
}

.h6 {
	@extend h6;
}

.section_title {
	margin-bottom: 23px;
}

.cole {
	padding-bottom: 35px;
}

.container-fluid {
	padding: 0 15px;
}

.main-content {
	margin-top: 160px;

	&.main-content--no-margin-desktop {
		margin-top: 0;
	}

	&.main-content--no-margin {
		margin-top: 0;
	}

	@media (max-width: 991px) {
		margin-top: 101px;

		&.main-content--no-margin-desktop {
			margin-top: 101px;
		}

		&.main-content--no-margin {
			margin-top: 0;
		}
	}

	@media (max-width: 767px) {
		margin-top: 142px;

		&.main-content--no-margin-desktop {
			margin-top: 142px;
		}

		&.main-content--no-margin {
			margin-top: 0;
		}
	}
}

.clearfix {
	&:after {
		content: '';
		display: table;
		clear: both;
	}

	*zoom: 1;
}

@mixin clearfix() {
	&:after {
		content: '';
		display: table;
		clear: both;
	}
	*zoom: 1;
}

@mixin prefixer($property, $value, $prefixes) {
	@each $prefix in $prefixes {
		@if $prefix == webkit {
			-webkit-#{$property}: $value;
		} @else if $prefix == moz {
			-moz-#{$property}: $value;
		} @else if $prefix == ms {
			-ms-#{$property}: $value;
		} @else if $prefix == o {
			-o-#{$property}: $value;
		} @else if $prefix == spec {
			#{$property}: $value;
		} @else {
			@warn "Unrecognized prefix: #{$prefix}";
		}
	}
}

@function darkenModified($variable, $opacity) {
	@return unquote('linear-gradient(to top,rgba(0, 0, 0,'+ $opacity +'),rgba(0, 0, 0,' + $opacity + '))' + $variable);
}

@function lightenModified($variable, $opacity) {
	@return unquote('linear-gradient(to top,rgba(255, 255, 255,'+ $opacity +'),rgba(255, 255, 255,' + $opacity + ')) ' + $variable);
}

@mixin transform($transform) {
	@include prefixer(transform, $transform, ms webkit spec);
}

@mixin user-select($value: none) {
	@include prefixer(user-select, $value, webkit moz ms spec);
}

@mixin backface($visibility: hidden) {
	@include prefixer(backface-visibility, $visibility, webkit spec);
}

@function em($target, $context: $baseFontSize) {
	@if $target == 0 {
		@return 0;
	}
	@return $target / $context + 0em;
}

@function color-control($color) {
	@if (lightness( $color ) > 40) {
		@return #000;
	} @else {
		@return #fff;
	}
}

$min: min-width;
$max: max-width;
@mixin at-query($constraint_, $viewport1_, $viewport2_:null) {
	$constraint: $constraint_;
	$viewport1: $viewport1_;
	$viewport2: $viewport2_;
	@if type-of($constraint_) == number {
		$viewport1: $constraint_;
		$viewport2: $viewport1_;
		$constraint: null;
	}
	@if $constraint == $min {
		@media screen and ($min: $viewport1) {
			@content;
		}
	} @else if $constraint == $max {
		@media screen and ($max: $viewport1) {
			@content;
		}
	} @else {
		@media screen and ($min: $viewport1) and ($max: $viewport2) {
			@content;
		}
	}
}

