	width: auto;
	margin: 0;
	line-height: 1.42;
	font-weight: bold;
	text-decoration: none;
	text-align: center;
	vertical-align: middle;
	white-space: nowrap;
	cursor: pointer;
	border: 1px solid transparent;
	@include user-select(none);
	-webkit-appearance: none;
	-moz-appearance: none;
	border-radius: $radius;
	/*================ Set primary button colors - can override later ================*/
	background-color: $colorBtnPrimary !important;
	color: $colorBtnPrimaryText !important;
}

/*============================================================================
  #Lists
==============================================================================*/
ul, ol {
	margin: 0 0 ($gutter / 2) 20px;
	padding: 0;
}

ul ul, ul ol,
ol ol, ol ul {
