}

li {
	margin-bottom: 0.25em;
}

/*============================================================================
  #Tables
==============================================================================*/
table {
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
}

th, td {
	text-align: left;
	padding: 0;
	border: none;
}

/*============================================================================
  #Images and Iframes
==============================================================================*/
img {
	border: 0 none;
}

svg:not(:root) {
	overflow: hidden;
}

img,
iframe {
	max-width: 100%;
}

/*============================================================================
  #Forms
==============================================================================*/
form {
	margin-bottom: 0;
}

/*================ Prevent zoom on touch devices in active inputs ================*/
input,
textarea,
button,
select {
	padding: 0;
	margin: 0;
	@include user-select(text);
}

button {
	background: none;
	border: none;
	cursor: pointer;
}

button,
input,
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
}

button {
	background: none;
	border: none;
	display: inline-block;
	cursor: pointer;
}

button,
input[type="submit"] {
	cursor: pointer;
}

input,
textarea,
select {
