.home-reviews {
	padding-bottom: 30px;
	background-color: var(--bg-color);

	.home-reviews__title {
		color: var(--title-color);
	}

	.home-reviews__wrapper {
		margin-top: 65px;
	}

	.home-reviews__slider-arrows {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 40px;
	}

	.home-reviews__slider-arrow {
		top: 50%;
		width: 40px;
		height: 40px;
		font-size: 25px;
		color: #414141;
		border-radius: 50%;
		border: 1px solid #ebebeb;
		line-height: 33px;
		background-color: #fff;
		display: inline-block;
		vertical-align: middle;
		text-align: center;
		cursor: pointer;
		transition-property: background-color, color, border-color;
		transition-duration: 0.4s;

		&.home-reviews__slider-arrow--prev {
			margin-right: 10px;

			i {
				margin-right: 2px;
			}
		}

		&.home-reviews__slider-arrow--next {
			margin-left: 10px;

			i {
				margin-left: 2px;
			}
		}

		&.slick-hidden {
			display: none;
		}

		&:hover {
			border-color: var(--color-primary);
			background-color: var(--color-primary);
			color: #fff;
		}
	}

	.home-reviews__item {
		display: flex;
		padding: 0 10px;
	}

	.home-reviews__item-image-wrapper {
		margin-right: 30px;
	}

	.home-reviews__item-image {
		width: 89px;
		height: 89px;
		border-radius: 50%;
		object-fit: cover;
	}

	.home-reviews__item-text {
		width: calc(100% - 119px);
	}


	.home-reviews__item-blockquote {
		position: relative;
		display: inline-block;
		padding: 0 16px;
		font-size: 18px;
		font-style: italic;
		color: var(--font-color);

		&::before, &::after {
			content: "\f10d";
			display: inline-block;
			width: auto;
			margin-left: -25px;
			margin-top: -1px;
			padding-right: 10px;
			vertical-align: top;
			font-family: 'Font Awesome 5 Free';
			font-weight: 900;
			font-size: 16px;
			color: var(--quotation-mark-color);
		}

		&::after {
			content: "\f10e";
			padding-left: 10px;
			padding-top: 8px;
			margin-left: 0;
			margin-right: -22px;
		}
	}

	.home-reviews__item-author-name {
		padding-left: 25px;
		margin-top: 25px;
		font-size: 16px;
		color: var(--author-color);
	}

	@media (max-width: 767px) {
		.home-reviews__item {
			flex-direction: column;
			padding: 0 10px;
		}

		.home-reviews__item-text {
			width: 100%;
		}

		.home-reviews__item-image-wrapper {
			margin-right: 0;
			margin-bottom: 25px;
		}

		.home-reviews__item-image {
			margin: 0 auto;
		}

		.home-reviews__item-text {
			text-align: center;
		}

		.home-reviews__item-blockquote {
			font-size: var(--mobile-font-size);
		}

		.home-reviews__item-author-name {
			padding-left: 0;
		}
	}
}

