		margin-bottom: 7px;
		padding-left: 10px;
		padding-right: 10px;

		&.collection-page__header--mobile {
			display: none;
		}

		&.collection-page__header--space {
			margin-top: 14px;
			margin-bottom: 14px;
		}
	}

	.collection-page__header-overlay {
		z-index: 99999;
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		opacity: 0;
		visibility: hidden;
		transition-property: visibility, opacity;
		transition-duration: 0.4s;

		&.collection-page__header-overlay--show {
			opacity: 1;
			visibility: visible;
		}
	}

	.collection-page__viewing {
		display: flex;
		align-items: center;
	}

	.collection-page__sort {
		display: flex;
		align-items: center;
		margin-left: 15px;
	}

	.collection-page__browse {
		margin-left: 15px;
	}

	.collection-page__showing {
		display: flex;
		align-items: center;
	}

	.collection-page__showing-button {
		padding: 0 8px;
		font-size: 20px;
		color: $colorBorder;

		&:hover {
			color: $colorPrimary;
		}

		&.collection-page__showing-button--active {
			cursor: default;
			color: $colorPrimary;
		}
	}

	.collection-page__wrapper {
		display: flex;

		&.collection-page__wrapper--top-space {
			margin-top: 20px;
		}
	}

	.collection-page__filter-row {
		width: 17%;
		padding-left: 10px;
	}

	.collection-page__products-row {
		width: 83%;

		&.collection-page__products-row--full-width {
			width: 100%;
		}
	}

	.collection-page__title {
		padding: 0 10px;
	}

	.collection-page__products-row-wrapper {
		display: flex;
		flex-wrap: wrap;
	}

	.collection-page__product {
		width: 33.33%;
		padding: 0 10px;
		margin-bottom: 20px;

		&.collection-page__product--four {
			width: 25%;
		}

		&.collection-page__product--two {
			width: 50%;
		}
	}

	.collection-page__tags {
		display: flex;
		flex-wrap: wrap;
		margin: 3px 0;
	}

	.collection-page__tag {
		margin-right: 15px;
		margin-bottom: 5px;

		a {
			display: inline-block;
			padding-top: 2px;
			padding-bottom: 2px;
			padding-right: 10px;
			font-size: 16px;
			color: #fff;
			background-color: #414141;
		}

		.fa-times {
			display: inline-block;
			padding: 6px;
			font-size: 15px;
			color: #fff;
			border-radius: 0;
			border: none;
			background-color: #313131;

			&::before {
				font-size: 15px;
			}
		}
	}

	.collection-page__tag-label {
		display: flex;
		align-items: center;
		margin-right: 15px;
		margin-bottom: 5px;
		font-size: 16px;
	}

	.collection-page__mobile-breadcrumbs {
		display: none;
	}

	.collection-page__product-list {
		margin-bottom: 20px;
		padding-left: 8px;
	}

	@media (max-width: 991px) {
		.collection-page__mobile-breadcrumbs {
			display: block;
		}

		.collection-page__header {
			display: none;
			padding-left: 0;
			padding-right: 0;

			&.collection-page__header--mobile {
				display: block;
				margin-bottom: 25px;
			}
		}

		.collection-page__filter-row {
			display: none;
		}

		.collection-page__products-row {
			width: 100%;
		}

		.collection-page__mobile-sort-filter-wrapper {
			position: relative;
			display: flex;
			justify-content: center;
		}

		.collection-page__mobile-sort {
			width: 50%;
			padding: 0 3px;

			&.open {
				z-index: 100000;
			}
		}

		.collection-page__mobile-sort-toggle {
			padding: 15px;
			border: 1px solid $colorBorder;
			font-size: 17px;
			text-transform: uppercase;
			text-align: center;
			line-height: 1;
			background-color: #fff;
		}

		.collection-page__mobile-sort-content {
			z-index: 100000;
			position: absolute;
			top: calc(100% + 10px);
			left: 0;
			width: 100%;
			padding: 20px 15px;
			border: 1px solid $colorBorder;
			background-color: #fff;
			opacity: 0;
			visibility: hidden;
			overflow-y: auto;
			transition-property: opacity, visibility;
			transition-duration: 0.3s;

			&.collection-page__mobile-sort-content--show {
				opacity: 1;
				visibility: visible;
			}
		}

		.collection-page__mobile-sort-item {
			margin-bottom: 3.5px;
			padding: 5px 0;
			font-size: 14px;
			text-transform: uppercase;
		}

		.collection-page__product {
			&.collection-page__product--four {
				width: 33.33%;
			}
		}
	}

	@media (max-width: 767px) {
		.collection-page__product {
			width: 50%;

			&.collection-page__product--four {
				width: 50%;
			}
		}
	}

	@media (max-width: 480px) {
		.collection-page__title {
			padding: 0;
		}

		.collection-page__mobile-sort {
			flex-grow: 1;
		}

		.collection-page__product {
			width: 100%;
			padding: 0;

			&.collection-page__product--four {
				width: 100%;
			}

			&.collection-page__product--two {
				width: 100%;
			}

			&.collection-page__product--mobile-two {
				width: 50%;
				padding: 0 5px;

				&.collection-page__product--four {
					width: 50%;
				}

				&.collection-page__product--two {
					width: 50%;
				}
			}
		}
	}
}

.collection-filters {
