	.collection-filters__category-title {
		position: relative;
		margin: 0;
		padding: 20px;
		font-size: 16px;
		color: #000;
		border-bottom: 1px solid #cecece;
		cursor: pointer;
		transition: opacity 0.3s;

		&::after {
			content: "+";
			position: absolute;
			right: 20px;
			top: 50%;
			font-size: 28px;
			font-weight: 700;
			color: #cecece;
			transform: translateY(-50%);
		}

		&:hover {
			opacity: 0.7;
		}
	}

	.collection-filters__category {
		&.open {
			.collection-filters__category-title {
				&::after {
					content: "-";
				}
			}
