	.collection-filters__category-list {
		display: none;
		padding: 20px;
	}

	.collection-filters__category-list-item {
		height: 21px;
		margin-bottom: 10px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	.collection-filters__category-list-item-link {
		position: relative;
		padding-left: 28px;

		.check {
			position: absolute;
			left: 0;
			top: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			height: 20px;
			width: 20px;
			border: 1px solid #b4d641;
			transform: translateY(-50%);
