.product-control {
	text-align: center;

	.product-control__default-select {
		display: none;
		width: 100%;
		margin-bottom: 20px;

		&.product-control__default-select--show {
			display: block;
		}
	}

	.product-control__radio {
		display: none;

		&:checked {
			+ .product-control__label {
				border-color: #000000;
			}
		}
	}

	.product-control__option {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		&.product-control__option--hidden {
			display: none;
		}
	}

	.product-control__value {
		padding-left: 5px;
		padding-right: 5px;
		padding-bottom: 15px;
	}

	.product-control__label {
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 25px;
		min-height: 25px;
		padding: 3px 6px;
		font-size: 14px;
		background-size: cover;
		border: solid 1px $colorPrimary;
		transition: border-color 0.3s;

		&:hover {
			border-color: #000;
		}

		&.product-control__label--circle {
			min-width: 25px;
			min-height: 25px;
			border-radius: 50%;
			font-size: 0;
		}

		&.product-control__label--square {
			width: 38px;
			height: 38px;
			font-size: 0;
		}
	}

	.product-control__button {
		width: fit-content;
		font-size: var(--quickview-font-size);
		font-weight: var(--quickview-font-style);
		text-transform: uppercase;
		color: var(--quickview-font-color);
		border-color: var(--quickview-border-color);
		background-color: var(--quickview-bg-color);
		transition-property: background-color, color;
		transition-duration: 0.3s;

		&:disabled {
			background-color: #f6f6f6;
			border-color: #dedede;
			color: var(--color-primary);
		}

		&:hover {
			background-color: var(--quickview-hover-bg-color);
			color: var(--quickview-hover-font-color);
		}
	}
